.button {
  display: inline-block;
  margin: 1rem 0;
  padding: .5rem .75rem;
  transition: background-color 0.15s cubic-bezier(0, 0, 0.2, 1), color 0.15s cubic-bezier(0, 0, 0.2, 1);
  border: 3px solid #1fd68d;
