    color: #ffffff; }

.form-input {
  display: block;
  width: 100%;
  padding: .5rem .75rem;
  transition: border-color 0.15s cubic-bezier(0, 0, 0.2, 1);
  border: 2px solid #868e96;
  border-radius: 0.5rem;
