  border: 2px solid #868e96;
  border-radius: .5rem;
  outline: none;
  background-color: #f8f9fa;
  color: #868e96; }
  .form-select:focus, .form-select:active {
    border-color: #1fd68d;
    background-color: #eefcf5; }
  .form-select:valid {
    color: #212529; }

.form-group {
  position: relative;
  margin-top: 1.5rem;
  margin-bottom: 1rem; }

.form-icon {
  position: absolute;
  top: .6rem;
  left: 1rem;
